const displayButton = () => {
  window.addEventListener('scroll', () => {
    if (window.scrollY > 100) {
      refs.button.style.display = 'block';
    } else {
      refs.button.style.display = 'none';
    }
  });
};

const scrollToTop = () => {
  refs.button.addEventListener('click', () => {
    window.scroll({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
  });
};

displayButton();
scrollToTop();
addDarkClassToHTML();
